import React, {useEffect, useState} from 'react';
import MiniNavMapElement from "./MiniNavMapElement";
import useTranslation from "../../../hooks/useTranslation";

const MiniNavMap = ({values}) => {

    // manage local state
    const [isActiveId, setActiveId] = useState(null);

    if(values.length === 0){
        return null;
    }

    // scroll handling
    useEffect(() => {

        const observer = new IntersectionObserver(function(entries) {
            entries.forEach((entry)=>{
                if(entry.isIntersecting === true){
                    setActiveId(entry.target.id);
                }
            });
        }, { threshold: [1] });

        values.map((value)=>{
            if(document.getElementById(value.id)){
                observer.observe(document.getElementById(value.id));

                value.fields && value.fields.map((field)=>{
                    if(document.getElementById(field.id)){
                        observer.observe(document.getElementById(field.id));
                    }
                });

                value.children && value.children.map((child)=>{
                    if(document.getElementById(child.id)){
                        observer.observe(document.getElementById(child.id));
                    }
                });

                value.blocks && value.blocks.map((block)=>{
                    if(document.getElementById(block.id)){
                        observer.observe(document.getElementById(block.id));

                        block.fields && block.fields.map((field)=>{
                            if(document.getElementById(field.id)){
                                observer.observe(document.getElementById(field.id));
                            }
                        });
                    }
                });
            }
        });

    }, []);

    return (
        <div className={`acpt-mini-map ${document.globals.is_rtl === true ? `rtl` : ``}`}>
            <h4>
                {useTranslation("Quick navigation")}
            </h4>
            {values.map((value, index) => (
                <MiniNavMapElement
                    value={value}
                    key={index}
                    isActiveId={isActiveId}
                    setActiveId={setActiveId}
                />
            ))}
        </div>
    );
};

export default MiniNavMap;