import React, {useState} from 'react';
import {Icon} from "@iconify/react";
import {scrollToId} from "../../../utils/scroll";

const MiniNavMapElement = ( {value, isActiveId, setActiveId}) => {

    const [isClosed, setClosed] = useState(false);

    const itemTitle = (val) => {

        if(val.title){
            return val.title;
        }

        if(val.name){
            return val.name;
        }

        if(val.pageTitle){
            return val.pageTitle;
        }

        return null;
    };

    const renderChildrenPages = (children) => {
        return (
            <ul className={isClosed ? 'closed' : 'open'}>
                {children.map((child, index) => {
                    return (
                        <React.Fragment>
                            <li className={child.parentId ? 'is-child' : ''} key={index}>
                                <a
                                    href="#"
                                    className={(isActiveId === child.id) ? 'active' : ''}
                                    onClick={e => {
                                        e.preventDefault();
                                        scrollToId(child.id);
                                        setActiveId(child.id);
                                    }}
                                >
                                    <span className="label field">C</span> {itemTitle(child)}
                                </a>
                            </li>
                        </React.Fragment>
                    );
                })}
            </ul>
        );
    };

    const renderChildrenElements = (children) => {
        return (
            <ul className={isClosed ? 'closed' : 'open'}>
                {children.map((child, index) => {
                    if(typeof child.parentId === 'undefined' || child.parentId === null){
                        return (
                            <React.Fragment>
                                <li className={child.parentId ? 'is-child' : ''} key={index}>
                                    <a
                                        href="#"
                                        className={(isActiveId === child.id) ? 'active' : ''}
                                        onClick={e => {
                                            e.preventDefault();
                                            scrollToId(child.id);
                                            setActiveId(child.id);
                                        }}
                                    >
                                        <span className="label field">F</span> {itemTitle(child)}
                                    </a>
                                </li>
                                {child.children && child.children.length > 0 && child.children.map((ch, chIndex)=>{
                                    return (
                                        <li className="is-child" key={chIndex}>
                                            <a
                                                href="#"
                                                className={(isActiveId === ch.id) ? 'active' : ''}
                                                onClick={e => {
                                                    e.preventDefault();
                                                    scrollToId(ch.id);
                                                    setActiveId(ch.id);
                                                }}
                                            >
                                                <span className="label child">C</span> {itemTitle(ch)}
                                            </a>
                                        </li>
                                    );
                                })}
                                {child.blocks && child.blocks.length > 0 && (
                                    renderBlocksElements(child.blocks)
                                )}
                            </React.Fragment>
                        );
                    }
                })}
            </ul>
        );
    };

    const renderBlocksElements = (blocks) => {

        return (
            <React.Fragment>
                {blocks.map((block)=>{
                    return (
                        <React.Fragment>
                            <li className="is-child">
                                <a
                                    href="#"
                                    className={(isActiveId === block.id) ? 'active' : ''}
                                    onClick={e => {
                                        e.preventDefault();
                                        scrollToId(block.id);
                                        setActiveId(block.id);
                                    }}
                                >
                                    <span className="label block">B</span> {block.name}
                                </a>
                            </li>
                            {block.fields && block.fields.map((field)=>(
                                <li className="is-grandchild">
                                    <a
                                        href="#"
                                        className={(isActiveId === field.id) ? 'active' : ''}
                                        onClick={e => {
                                            e.preventDefault();
                                            scrollToId(field.id);
                                            setActiveId(field.id);
                                        }}
                                    >
                                        <span className="label child">C</span> {field.name}
                                    </a>
                                </li>
                            ))}
                        </React.Fragment>
                    );
                })}
            </React.Fragment>
        );
    };

    return (
        <div className="mini-map-element">
            <div className="box">
                <a href="#"
                    onClick={e => {
                        e.preventDefault();
                        setClosed(!isClosed);
                    }}
                >
                    <Icon icon={`bx:bx-chevron-${isClosed ? 'up' : 'down'}`} width="18px"/>
                </a>
                <a
                    href="#"
                    className={`title ${(isActiveId === value.id) ? 'active' : ''}`}
                    onClick={e => {
                    e.preventDefault();
                    scrollToId(value.id);
                    setActiveId(value.id);
                }}>
                    {itemTitle(value)}
                </a>
            </div>
            {value.fields && value.fields.length > 0 && renderChildrenElements(value.fields)}
            {value.children && value.children.length > 0 && renderChildrenPages(value.children)}
        </div>
    );
};

export default MiniNavMapElement;